import React, { Component} from 'react';
import ReactDOM from 'react-dom';
import { Layout, Menu, Icon } from 'antd';
import Home from './comtainers/home/';
import Resume from './comtainers/resume/';
import Fate from './comtainers/fate/';
import Record from './comtainers/record/';

import './index.css';
import 'antd/dist/antd.css';


const { Footer, Sider, Content } = Layout;

class App extends Component{
    constructor(props){
        super(props);
        this.state={
            collWidth: 80,
            breakPoint: 'lg',
            pageName: <Home />
        }
    }
    componentWillMount() {
        window.addEventListener('resize', this.onWindowResize.bind(this));
    }
    onWindowResize(e){
        let winWidth=e.currentTarget.innerWidth;
        if(winWidth <= 480){
            this.setState({
                breakPoint: 'xs',
                collWidth: 0
            })
        }else{
            this.setState({
                breakPoint: 'xxl',
                collWidth: 80
            })
        }
    }
    goHome(){
        this.setState({
            pageName: <Home />
        })
    }
    goResume(){
        this.setState({
            pageName: <Resume />
        })
    }
    goFate(){
        this.setState({
            pageName: <Fate />
        })
    }
    goRecord(){
        this.setState({
            pageName: <Record />
        })
    }
    render(){
        return(
            <Layout style={{ minHeight: '100vh'}}>
                <Sider breakpoint={this.state.breakPoint} collapsedWidth={this.state.collWidth} collapsible='true'>
                    <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
                        <Menu.Item key="1" onClick={this.goHome.bind(this)}>
                            <Icon type="home" />
                            <span className="nav-text">首页</span>
                        </Menu.Item>
                        <Menu.Item key="2" onClick={this.goResume.bind(this)}>
                            <Icon type="file-text" />
                            <span className="nav-text">简历</span>
                        </Menu.Item>
                        <Menu.Item key="3" onClick={this.goFate.bind(this)}>
                            <Icon type="youtube" />
                            <span className="nav-text">Fate</span>
                        </Menu.Item>
                        <Menu.Item key="4" onClick={this.goRecord.bind(this)}>
                            <Icon type="edit" />
                            <span className="nav-text">记录</span>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Layout>
                    <Content>
                        {this.state.pageName}
                    </Content>
                    <Footer style={{ textAlign: 'center' }}>Ant Design ©2019 Created by Ant UED</Footer>
                </Layout>
            </Layout>
        )
    }
}
ReactDOM.render(<App />, document.getElementById('root'));
